"use client"

import { Button } from '@/components/ui/button'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'
import { Badge } from '@/components/ui/badge'
import { Separator } from '@/components/ui/separator'
import { Alert, AlertDescription } from '@/components/ui/alert'
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table'

interface SubstanceResult {
  name: string
  score: number
  riskLevel: string
  intervention: string
  frequency?: number
  problems?: number
  failedAttempts?: number
  concern?: number
  warnings?: number
  quitAttempts?: number
  injection?: number
}

interface ResultsData {
  patientInfo: {
    name: string
    id: string
    institution: string
    date: string
  }
  substances: SubstanceResult[]
  totalScore: number
  screeningDate: string
}

interface ResultsDisplayProps {
  data: ResultsData
  onNewScreening: () => void
}

export default function ResultsDisplayComplete({ data, onNewScreening }: ResultsDisplayProps) {
  const getRiskColor = (riskLevel: string) => {
    switch (riskLevel) {
      case 'Tidak Ada Intervensi':
        return 'bg-green-100 text-green-800'
      case 'Intervensi Singkat':
        return 'bg-yellow-100 text-yellow-800'
      case 'Pengobatan Intensif':
        return 'bg-red-100 text-red-800'
      default:
        return 'bg-gray-100 text-gray-800'
    }
  }

  const getInterventionRecommendation = (riskLevel: string) => {
    switch (riskLevel) {
      case 'Tidak Ada Intervensi':
        return 'Tidak diperlukan intervensi khusus. Lanjutkan pola hidup sehat dan edukasi preventif.'
      case 'Intervensi Singkat':
        return 'Edukasi tentang risiko penggunaan zat, konseling motivasional, dan follow-up berkala.'
      case 'Pengobatan Intensif':
        return 'Rujukan ke spesialis, terapi rehabilitasi komprehensif, dan monitoring intensif.'
      default:
        return 'Konsultasikan dengan tenaga kesehatan profesional.'
    }
  }

  const getAlcoholInterventionLevel = (score: number): string => {
    if (score >= 27) return 'Pengobatan Intensif'
    if (score >= 11) return 'Intervensi Singkat'
    return 'Tidak Ada Intervensi'
  }

  const calculateDetailedScore = (substance: SubstanceResult) => {
    const p2 = substance.frequency || 0
    const p3 = substance.problems || 0
    const p4 = substance.failedAttempts || 0
    const p5 = substance.concern || 0
    const p6 = substance.warnings || 0
    const p7 = substance.quitAttempts || 0
    const p8 = substance.injection || 0 // P8 tidak dihitung dalam skor total
    
    const totalScore = p2 + p3 + p4 + p5 + p6 + p7 // P8 tidak termasuk
    
    return {
      p2, p3, p4, p5, p6, p7, p8,
      totalScore
    }
  }

  const calculateTotalRiskScore = () => {
    return data.substances.reduce((total, substance) => {
      const detailed = calculateDetailedScore(substance)
      return total + detailed.totalScore
    }, 0)
  }

  const getOverallRiskAssessment = () => {
    const totalScore = calculateTotalRiskScore()
    const highRiskSubstances = data.substances.filter(s => {
      const detailed = calculateDetailedScore(s)
      return detailed.totalScore >= 27
    }).length
    
    const moderateRiskSubstances = data.substances.filter(s => {
      const detailed = calculateDetailedScore(s)
      return detailed.totalScore >= 4 && detailed.totalScore < 27
    }).length

    if (highRiskSubstances > 0) {
      return {
        level: 'Pengobatan Intensif',
        description: 'Memerlukan pengobatan intensif dan rujukan ke spesialis',
        color: 'bg-red-100 text-red-800',
        substances: highRiskSubstances
      }
    } else if (moderateRiskSubstances > 0) {
      return {
        level: 'Intervensi Singkat',
        description: 'Memerlukan intervensi singkat dan monitoring berkala',
        color: 'bg-yellow-100 text-yellow-800',
        substances: moderateRiskSubstances
      }
    } else {
      return {
        level: 'Tidak Ada Intervensi',
        description: 'Tidak diperlukan intervensi khusus',
        color: 'bg-green-100 text-green-800',
        substances: 0
      }
    }
  }

  const overallAssessment = getOverallRiskAssessment()

  const handlePrint = () => {
    window.print()
  }

  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleDateString('id-ID', {
      weekday: 'long',
      year: 'numeric',
      month: 'long',
      day: 'numeric'
    })
  }

  return (
    <div className="space-y-6 print:space-y-4">
      {/* Header Information */}
      <div className="print:break-inside-avoid">
        <Card>
          <CardHeader>
            <CardTitle className="text-center text-xl">
              Hasil Screening WHO-ASSIST V3.1
            </CardTitle>
            <p className="text-center text-sm text-gray-600 mt-2">
              Alat Skrining Zat WHO - Versi 3.1 (Lengkap P1-P8)
            </p>
          </CardHeader>
          <CardContent>
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4 text-sm">
              <div>
                <strong>Nama Petugas:</strong> {data.patientInfo?.name || '-'}
              </div>
              <div>
                <strong>Nama Lembaga:</strong> {data.patientInfo?.institution || '-'}
              </div>
              <div>
                <strong>ID Klien:</strong> {data.patientInfo?.id || '-'}
              </div>
              <div>
                <strong>Tanggal Screening:</strong> {formatDate(data.patientInfo?.date)}
              </div>
            </div>
          </CardContent>
        </Card>
      </div>

      {/* Overall Assessment */}
      <div className="print:break-inside-avoid">
        <Card>
          <CardHeader>
            <CardTitle>Ringkasan Hasil Screening</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
              <div className="text-center">
                <Badge className={`${overallAssessment.color} text-lg px-4 py-2 mb-2`}>
                  {overallAssessment.level}
                </Badge>
                <p className="text-sm text-gray-600">{overallAssessment.description}</p>
              </div>
              <div className="text-center">
                <div className="text-3xl font-bold text-gray-900">
                  {calculateTotalRiskScore()}
                </div>
                <div className="text-sm text-gray-600">Total Skor Keseluruhan</div>
              </div>
              <div className="text-center">
                <div className="text-3xl font-bold text-gray-900">
                  {data.substances?.length || 0}
                </div>
                <div className="text-sm text-gray-600">Zat yang Digunakan</div>
              </div>
            </div>
            
            <Separator className="my-4" />
            
            <Alert>
              <AlertDescription>
                <strong>Rekomendasi Intervensi:</strong> {getInterventionRecommendation(overallAssessment.level)}
              </AlertDescription>
            </Alert>
          </CardContent>
        </Card>
      </div>

      {/* Detailed Calculation Table */}
      <div className="print:break-inside-avoid">
        <Card>
          <CardHeader>
            <CardTitle>Perhitungan Detail per Zat (P1-P8)</CardTitle>
          </CardHeader>
          <CardContent>
            {data.substances?.length === 0 ? (
              <p className="text-center text-gray-600 py-8">
                Tidak ada zat yang digunakan dalam 3 bulan terakhir.
              </p>
            ) : (
              <div className="overflow-x-auto">
                <Table>
                  <TableHeader>
                    <TableRow>
                      <TableHead className="w-[120px]">Zat</TableHead>
                      <TableHead className="text-center">P2<br/><span className="text-xs">Frekuensi</span></TableHead>
                      <TableHead className="text-center">P3<br/><span className="text-xs">Keinginan</span></TableHead>
                      <TableHead className="text-center">P4<br/><span className="text-xs">Masalah</span></TableHead>
                      <TableHead className="text-center">P5<br/><span className="text-xs">Kegagalan</span></TableHead>
                      <TableHead className="text-center">P6<br/><span className="text-xs">Peringatan</span></TableHead>
                      <TableHead className="text-center">P7<br/><span className="text-xs">Upaya Berhenti</span></TableHead>
                      <TableHead className="text-center">P8<br/><span className="text-xs">Menyuntik*</span></TableHead>
                      <TableHead className="text-center">Total<br/><span className="text-xs">Skor</span></TableHead>
                      <TableHead className="text-center">Intervensi<br/><span className="text-xs">Yang Dibutuhkan</span></TableHead>
                    </TableRow>
                  </TableHeader>
                  <TableBody>
                    {data.substances?.map((substance, index) => {
                      const detailed = calculateDetailedScore(substance)
                      let interventionLevel = 'Tidak Ada Intervensi'
                      if (substance.name === 'Minuman beralkohol (bir, anggur, miras, dll.)') {
                        if (detailed.totalScore >= 27) {
                          interventionLevel = 'Pengobatan Intensif'
                        } else if (detailed.totalScore >= 11) {
                          interventionLevel = 'Intervensi Singkat'
                        } else {
                          interventionLevel = 'Tidak Ada Intervensi'
                        }
                      } else {
                        if (detailed.totalScore >= 27) {
                          interventionLevel = 'Pengobatan Intensif'
                        } else if (detailed.totalScore >= 4) {
                          interventionLevel = 'Intervensi Singkat'
                        }
                      }
                      
                      return (
                        <TableRow key={index}>
                          <TableCell className="font-medium text-xs">{substance.name}</TableCell>
                          <TableCell className="text-center">{detailed.p2}</TableCell>
                          <TableCell className="text-center">{detailed.p3}</TableCell>
                          <TableCell className="text-center">{detailed.p4}</TableCell>
                          <TableCell className="text-center">{detailed.p5}</TableCell>
                          <TableCell className="text-center">{detailed.p6}</TableCell>
                          <TableCell className="text-center">{detailed.p7}</TableCell>
                          <TableCell className="text-center text-gray-500">{detailed.p8}</TableCell>
                          <TableCell className="text-center font-bold">{detailed.totalScore}</TableCell>
                          <TableCell className="text-center">
                            <Badge className={`${getRiskColor(interventionLevel)} text-xs`}>
                              {interventionLevel}
                            </Badge>
                          </TableCell>
                        </TableRow>
                      )
                    })}
                  </TableBody>
                </Table>
                <p className="text-xs text-gray-500 mt-2">
                  *P8 (Menyuntik) tidak dihitung dalam skor total, tetapi penting untuk menentukan tingkat risiko dan intervensi
                </p>
              </div>
            )}
          </CardContent>
        </Card>
      </div>

      {/* Risk Level Summary */}
      <div className="print:break-inside-avoid">
        <Card>
          <CardHeader>
            <CardTitle>Ringkasan Tingkat Intervensi</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
              <div className="text-center p-4 border rounded-lg">
                <div className="text-2xl font-bold text-green-600">
                  {data.substances?.filter(s => {
                    const score = calculateDetailedScore(s).totalScore
                    return s.name === 'Minuman beralkohol (bir, anggur, miras, dll.)'
                      ? score < 11
                      : score < 4
                  }).length || 0}
                </div>
                <div className="text-sm text-gray-600">Tidak Ada Intervensi</div>
              </div>
              <div className="text-center p-4 border rounded-lg">
                <div className="text-2xl font-bold text-yellow-600">
                  {data.substances?.filter(s => {
                    const score = calculateDetailedScore(s).totalScore
                    if (s.name === 'Minuman beralkohol (bir, anggur, miras, dll.)') {
                      return score >= 11 && score < 27
                    } else {
                      return score >= 4 && score < 27
                    }
                  }).length || 0}
                </div>
                <div className="text-sm text-gray-600">Intervensi Singkat</div>
              </div>
              <div className="text-center p-4 border rounded-lg">
                <div className="text-2xl font-bold text-red-600">
                  {data.substances?.filter(s => calculateDetailedScore(s).totalScore >= 27).length || 0}
                </div>
                <div className="text-sm text-gray-600">Pengobatan Intensif</div>
              </div>
            </div>
          </CardContent>
        </Card>
      </div>

      {/* Injection Risk Assessment */}
      <div className="print:break-inside-avoid">
        <Card>
          <CardHeader>
            <CardTitle>Penilaian Risiko Menyuntik (P8)</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="space-y-4">
              {data.substances?.filter(s => (s.injection || 0) > 0).length > 0 ? (
                <div>
                  <Alert className="border-orange-200 bg-orange-50">
                    <AlertDescription>
                      <strong>Perhatian:</strong> Klien pernah menggunakan zat dengan cara menyuntik. Diperlukan asesmen lebih lanjut tentang pola menyuntik dan risiko penularan penyakit.
                    </AlertDescription>
                  </Alert>
                  <div className="mt-4">
                    <h4 className="font-medium mb-2">Zat yang Pernah Disuntik:</h4>
                    <ul className="list-disc list-inside space-y-1 text-sm">
                      {data.substances?.filter(s => (s.injection || 0) > 0).map((substance, index) => (
                        <li key={index}>
                          {substance.name} - {substance.injection === 2 ? 'Dalam 3 bulan terakhir' : 'Tidak dalam 3 bulan terakhir'}
                        </li>
                      ))}
                    </ul>
                  </div>
                </div>
              ) : (
                <p className="text-gray-600">Tidak ada riwayat penggunaan zat dengan cara menyuntik.</p>
              )}
            </div>
          </CardContent>
        </Card>
      </div>

      {/* Scoring Guidelines */}
      <div className="print:break-inside-avoid">
        <Card>
          <CardHeader>
            <CardTitle>Panduan Interpretasi Skor WHO-ASSIST V3.1</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="space-y-4">
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div className="space-y-3">
                  <h4 className="font-medium">Klasifikasi Skor WHO-ASSIST V3.1</h4>
                  <div className="space-y-2 text-sm">
                    <div className="flex items-center space-x-3">
                      <Badge className="bg-green-100 text-green-800 w-24 justify-center">0-3</Badge>
                      <span>Tidak Ada Intervensi</span>
                    </div>
                    <div className="flex items-center space-x-3">
                      <Badge className="bg-yellow-100 text-yellow-800 w-24 justify-center">4-26</Badge>
                      <span>Intervensi Singkat</span>
                    </div>
                    <div className="flex items-center space-x-3">
                      <Badge className="bg-red-100 text-red-800 w-24 justify-center">≥27</Badge>
                      <span>Pengobatan Intensif</span>
                    </div>
                  </div>
                </div>
                
                <div className="space-y-3">
                  <h4 className="font-medium">Pertanyaan WHO-ASSIST V3.1</h4>
                  <div className="space-y-1 text-sm text-gray-600">
                    <div><strong>P2:</strong> Frekuensi penggunaan dalam 3 bulan terakhir</div>
                    <div><strong>P3:</strong> Keinginan kuat untuk menggunakan</div>
                    <div><strong>P4:</strong> Masalah kesehatan, sosial, hukum, keuangan</div>
                    <div><strong>P5:</strong> Kegagalan melakukan tugas harian</div>
                    <div><strong>P6:</strong> Peringatan dari orang lain</div>
                    <div><strong>P7:</strong> Upaya berhenti yang gagal</div>
                    <div><strong>P8:</strong> Penggunaan dengan cara menyuntik*</div>
                  </div>
                </div>
              </div>
            </div>
          </CardContent>
        </Card>
      </div>

      {/* Action Buttons */}
      <div className="flex space-x-4 print:hidden">
        <Button onClick={handlePrint} variant="outline" className="flex-1">
          Cetak Hasil
        </Button>
        <Button onClick={onNewScreening} className="flex-1">
          Screening Baru
        </Button>
      </div>

      {/* Print Styles */}
      <style jsx global>{`
        @media print {
          body { 
            font-size: 12px; 
            line-height: 1.4;
          }
          .print\\:break-inside-avoid {
            break-inside: avoid;
          }
          .print\\:hidden {
            display: none !important;
          }
          .print\\:space-y-4 > * + * {
            margin-top: 1rem;
          }
          @page {
            margin: 1cm;
            size: A4;
          }
        }
      `}</style>
    </div>
  )
}
