"use client"

import { useState } from 'react'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Alert, AlertDescription } from '@/components/ui/alert'
import WhoAssistFormComplete from '@/components/WhoAssistFormComplete'
import ResultsDisplayComplete from '@/components/ResultsDisplayComplete'

export default function Home() {
  const [showResults, setShowResults] = useState(false)
  const [screeningData, setScreeningData] = useState<any>(null)

  const handleScreeningComplete = (data: any) => {
    setScreeningData(data)
    setShowResults(true)
  }

  const handleNewScreening = () => {
    setShowResults(false)
    setScreeningData(null)
  }

  return (
    <div className="min-h-screen bg-gray-50 py-8">
      <div className="container mx-auto max-w-4xl px-4">
        <div className="mb-8 text-center">
          <h1 className="text-3xl font-bold text-gray-900 mb-2">
            WHO-ASSIST V3.1 Screening Tool
          </h1>
          <p className="text-gray-600">
            Alat Skrining Zat WHO - Versi 3.1 untuk BNNP Sumsel
          </p>
        </div>

        <Card>
          <CardHeader>
            <CardTitle>Identifikasi Penggunaan Zat</CardTitle>
            <CardDescription>
              Kuesioner ini digunakan untuk mengidentifikasi penggunaan zat berbahaya dan tingkat risiko yang terkait
            </CardDescription>
          </CardHeader>
          <CardContent>
            {!showResults ? (
              <WhoAssistFormComplete onComplete={handleScreeningComplete} />
            ) : (
              <ResultsDisplayComplete 
                data={screeningData} 
                onNewScreening={handleNewScreening} 
              />
            )}
          </CardContent>
        </Card>

        <Alert className="mt-6">
          <AlertDescription>
            <div className="space-y-2 text-sm">
              <p><strong>Catatan Penting:</strong></p>
              <ul className="list-disc list-inside space-y-1 ml-4">
                <li>Hasil screening ini bersifat informatif dan harus dikonsultasikan dengan tenaga kesehatan profesional</li>
                <li><strong>Klasifikasi Skor WHO-ASSIST V3.1:</strong></li>
                <li className="ml-4">• 0-3: Tidak Ada Intervensi</li>
                <li className="ml-4">• 4-26: Intervensi Singkat</li>
                <li className="ml-4">• ≥27: Pengobatan Intensif</li>
              </ul>
            </div>
          </AlertDescription>
        </Alert>
      </div>
    </div>
  )
}
